import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import './index.css';
import { geocodeByAddress, getLatLng } from 'react-places-autocomplete';

const FormComponent = () => {
  const [images, setImages] = useState([]);
  const [userLocation, setUserLocation] = useState({ latitude: '', longitude: '', city: '', address: '' });
  const autoCompleteRef = useRef(null);

  useEffect(() => {
    const getLocation = async () => {
      try {
        if ('geolocation' in navigator) {
          navigator.geolocation.getCurrentPosition(
            async (position) => {
              const { latitude, longitude } = position.coords;
              setUserLocation({ latitude, longitude });
              fetchLocationCity(latitude, longitude);
            },
            (error) => {
              console.error('Error fetching location:', error.message);
            }
          );
        } else {
          console.error('Geolocation is not supported by this browser.');
        }
      } catch (error) {
        console.error('Error fetching location:', error.message);
      }
    };

    getLocation();
  }, []);

  const fetchLocationCity = async (latitude, longitude) => {
    try {
      const apiKey = '585c52d7644c7ac51c29b2dd22d49b58';
      const response = await axios.get(`https://api.openweathermap.org/data/2.5/weather?lat=${latitude}&lon=${longitude}&appid=${apiKey}`);
      const city = response.data.name;
      setUserLocation((prevLocation) => ({ ...prevLocation, city }));
    } catch (error) {
      console.error('Error fetching city:', error.message);
    }
  };

  const handleAddressChange = (address) => {
    setUserLocation({ ...userLocation, address });
  };

  const handleSelect = async (address) => {
    try {
      const results = await geocodeByAddress(address);
      const latLng = await getLatLng(results[0]);
      setUserLocation({
        ...userLocation,
        address,
        latitude: latLng.lat,
        longitude: latLng.lng,
      });
    } catch (error) {
      console.error('Error selecting address:', error.message);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const formData = new FormData(e.target);
    formData.append('latitude', userLocation.latitude);
    formData.append('longitude', userLocation.longitude);

    try {
      const response = await axios.post('https://ur32uy2l4polsbprzvbmqdn4si0hartc.lambda-url.us-east-2.on.aws/', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });

      console.log('Form submitted successfully:', response.data);
      setImages([]);
    } catch (error) {
      console.error('Error submitting form:', error.response?.data ?? 'No error response data');
    }
  };

  useEffect(() => {
    const autoComplete = new window.google.maps.places.Autocomplete(autoCompleteRef.current, { types: ['address'] });
    autoComplete.addListener('place_changed', () => {
      const place = autoComplete.getPlace();
      handleSelect(place.formatted_address);
    });
  }, []);

  return (
    <>
      <h2 id="report">Report a Bloom Form</h2>

      <div className="form-container">
        <form onSubmit={handleSubmit}>
          <input type="text" name="name" placeholder="Enter Name" required />
          <input type="text" name="phoneNumber" placeholder="Phone Number" required />
          <input type="email" name="email" placeholder="Email" required />
          <input
            type="text"
            name="address"
            placeholder="Enter Address"
            value={userLocation.address}
            onChange={(e) => handleAddressChange(e.target.value)}
            ref={autoCompleteRef}
            required
          />
          <input name="city" value={userLocation.city} readOnly />
          <input type="hidden" name="latitude" value={userLocation.latitude} />
          <input type="hidden" name="longitude" value={userLocation.longitude} />
          <input type="file" name="images" onChange={(e) => setImages(Array.from(e.target.files))} multiple required />
          <button type="submit">Submit</button>
        </form>
      </div>
    </>
  );
};

export default FormComponent;
